* {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: 0;
          box-sizing: 0;
}

html {
  height: 100%;
}

body {
  min-height: 100%;
  position: relative;
  font-family: "arial";
  /* SVG container */
}
body .container {
  width: 1200px;
  height: 740px;
  margin: auto;
  position: relative;
  /* Tooltip */
  /* SVG */
}
body .container .tooltip {
  background-color: rgba(255, 255, 255, 0.7);
  padding: 5px;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  pointer-events: none;
  font-size: 14px;
  font-weight: 600;
  border-radius: 5px;
}
body .container .tooltip p {
  padding: 2px;
}
body .container svg {
  margin-top: 40px;
  background-color: #adccec;
}
body .container svg path {
  fill: #070916;
}
body .container svg .meteorite {
  opacity: 0.7;
}



